// Add this to your _app.js or layout.js (wherever your root component is)

useEffect(() => {
  // Override the removeChild method to catch the error with stack trace
  const originalRemoveChild = Node.prototype.removeChild;

  Node.prototype.removeChild = function (child) {
    try {
      return originalRemoveChild.call(this, child);
    } catch (error) {
      console.error("ðŸ”´ removeChild Error Details:");
      console.error("Error:", error.message);
      console.error("Parent Node:", this);
      console.error("Child Node:", child);
      console.error("Stack trace:", error.stack);

      // Log the component hierarchy
      let currentElement = this;
      const hierarchy = [];
      while (currentElement && currentElement.parentElement) {
        hierarchy.push({
          tagName: currentElement.tagName,
          className: currentElement.className,
          id: currentElement.id,
          innerHTML: currentElement.innerHTML?.substring(0, 100) + "...",
        });
        currentElement = currentElement.parentElement;
      }
      console.error("DOM Hierarchy:", hierarchy);

      // Don't throw the error, just log it
      return null;
    }
  };

  // Cleanup on unmount
  return () => {
    Node.prototype.removeChild = originalRemoveChild;
  };
}, []);

// ALSO: Add this error boundary component to catch React errors
import React from "react";

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false, error: null, errorInfo: null };
  }

  static getDerivedStateFromError(error) {
    return { hasError: true };
  }

  componentDidCatch(error, errorInfo) {
    console.error("ðŸ”´ React Error Boundary Caught:");
    console.error("Error:", error);
    console.error("Error Info:", errorInfo);
    console.error("Component Stack:", errorInfo.componentStack);

    this.setState({
      error: error,
      errorInfo: errorInfo,
    });
  }

  render() {
    if (this.state.hasError) {
      return (
        <div
          style={{
            padding: "20px",
            backgroundColor: "#fee",
            border: "1px solid #f00",
          }}
        >
          <h2>Something went wrong.</h2>
          <details style={{ whiteSpace: "pre-wrap" }}>
            {this.state.error && this.state.error.toString()}
            <br />
            {this.state.errorInfo.componentStack}
          </details>
        </div>
      );
    }

    return this.props.children;
  }
}

// Wrap your app with this ErrorBoundary
export default ErrorBoundary;
